import Vuex from 'vuex'
import Vue from 'vue'
Vue.use(Vuex)      //indexmain.js

const actions = {

}
const mutations = {
    login(state,value){
        state.isLogin = value
    },
    userNameChange(state,value){
        state.userName = value
    },
    userIdChange(state,value){
        state.userId = value
    }
}
const state = {
    isLogin: false,
    userName:'',
    userId:''
}
export default new Vuex.Store({
    actions,
    mutations,
    state
})

